/* src/pages/Contact.css */
.contact-page {
  max-width: 1800px;
  margin: 0 auto;
  padding: 1rem;
  margin-top: 50px;
}

.contact-header {
  text-align: center;
  margin-bottom: 3rem;
  padding: 0 1rem;
}

.contact-header h1 {
  font-size: 2.5rem;
  color: var(--primary);
  margin-bottom: 1rem;
  position: relative;
  display: inline-block;
}

.contact-header h1::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 4px;
  background: var(--secondary);
  border-radius: 2px;
}

.contact-header p {
  font-size: 1.1rem;
  color: var(--gray);
  max-width: 700px;
  margin: 0 auto;
  line-height: 1.7;
}

.contact-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

@media (max-width: 900px) {
  .contact-container {
    grid-template-columns: 1fr;
  }
}

/* Contact Info Card */
.contact-info-card {
  background: linear-gradient(135deg, #f8f9fa 0%, #daece2 100%);
  border-radius: 12px;
  padding: 2rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  border: 1px solid #e0e6ed;
}

.contact-info-section {
  margin-bottom: 2.5rem;
}

.contact-info-section:last-child {
  margin-bottom: 0;
}

.contact-info-section h2 {
  font-size: 1.5rem;
  color: var(--primary);
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.section-icon {
  font-size: 1.3rem;
}

.contact-detail {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
  align-items: flex-start;
}

.contact-icon {
  background: var(--primary);
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  font-size: 1rem;
}

.contact-detail h3 {
  font-size: 1.1rem;
  margin-bottom: 0.3rem;
  color: var(--primary);
}

.contact-detail p {
  color: var(--gray);
  line-height: 1.6;
  font-size: 0.95rem;
}

/* Map Section */
.map-container {
  background: white;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
}

.map-placeholder {
  height: 250px;
  background: linear-gradient(rgba(44, 62, 80, 0.05), rgba(44, 62, 80, 0.05)), url('../assets/map.jpg') center/cover;
  position: relative;
}

.map-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(26, 58, 108, 0.1);
}

.map-marker {
  position: absolute;
  top: 50%;
  left: 70%;
  transform: translate(-50%, -50%);
}

.marker-pin {
  width: 30px;
  height: 30px;
  background: var(--secondary);
  border-radius: 50% 50% 50% 0;
  position: absolute;
  transform: rotate(-45deg);
  left: 50%;
  top: 0;
  margin: -15px 0 0 -15px;
}

.marker-pin::after {
  content: '';
  width: 18px;
  height: 18px;
  background: white;
  position: absolute;
  border-radius: 50%;
  top: 6px;
  left: 6px;
}

.marker-label {
  position: absolute;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
  background: var(--primary);
  color: white;
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 0.85rem;
  white-space: nowrap;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.map-instructions {
  padding: 1rem;
  text-align: center;
  color: var(--gray);
  font-size: 0.9rem;
  border-top: 1px solid #eee;
}

/* Contact Form Card */
.contact-form-card {
  background: white;
  border-radius: 12px;
  padding: 2.5rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  border: 1px solid #e0e6ed;
}

.form-header {
  margin-bottom: 1.5rem;
}

.form-header h2 {
  font-size: 1.7rem;
  color: var(--primary);
  margin-bottom: 0.5rem;
}

.form-header p {
  color: var(--gray);
  font-size: 1rem;
  line-height: 1.6;
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  font-weight: 600;
  color: var(--primary);
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.input-icon {
  color: var(--primary);
  font-size: 0.9rem;
}

.form-group input,
.form-group select,
.form-group textarea {
  padding: 0.9rem 1.2rem;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: #f9fbfd;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(26, 58, 108, 0.2);
}

.form-group textarea {
  resize: vertical;
  min-height: 150px;
}

/* Success Message */
.success-message {
  background: #e8f5e9;
  border-left: 4px solid #4caf50;
  padding: 1.2rem;
  border-radius: 0 8px 8px 0;
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.success-icon {
  background: #4caf50;
  color: white;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  font-weight: bold;
}

.success-message h3 {
  color: var(--primary);
  margin-bottom: 0.3rem;
}

.success-message p {
  color: var(--gray);
  font-size: 0.95rem;
}

/* Submit Button */
.submit-btn {
  background: var(--primary);
  color: white;
  border: none;
  padding: 1.1rem;
  font-size: 1.05rem;
  font-weight: 600;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
  margin-top: 0.5rem;
}

.submit-btn:hover {
  background: #0f2a50;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(26, 58, 108, 0.3);
}

.submit-btn .btn-icon {
  font-size: 1rem;
}

/* Loading Spinner */
.submit-btn.loading {
  background: #bdc3c7;
  pointer-events: none;
}

.spinner {
  width: 20px;
  height: 20px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}