.home-page {
  max-width: 1800px;
  margin: 0 auto;
}

/* Hero Section */
.hero {
  background: linear-gradient(to top, rgba(0, 46, 14, 0.5) 10%, rgba(0, 0, 0, 0.4) 20%, rgba(0,0,0,0)), url('../assets/campus.png') center/cover no-repeat;
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: relative;
  color: white;
}

.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(26, 93, 26, 0.8), rgba(44, 62, 80, 0.9));
}

.hero-content {
  position: relative;
  z-index: 2;
  max-width: 1200px;
  width: 100%;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hero-text h1 {
  max-width: 800px;
  margin-bottom: 3rem;
  color: rgb(37, 92, 52);
}

.hero h1 {
  font-size: 4rem;
  margin-bottom: 1.5rem;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.2);
  font-weight: 800;
  line-height: 1.2;
}

.hero p {
  font-size: 1.5rem;
  margin-bottom: 2rem;
  text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.5);
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
}

.hero-buttons {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  flex-wrap: wrap;
  margin-top: 2rem;
}

.btn {
  display: inline-block;
  padding: 1rem 2.5rem;
  border-radius: 30px;
  font-weight: 600;
  text-align: center;
  transition: all 0.3s ease;
  cursor: pointer;
  border: none;
  text-decoration: none;
}

.btn.primary {
  background-color: var(--primary);
  color: white;
  box-shadow: 0 4px 15px rgba(26, 93, 26, 0.4);
}

.btn.primary:hover {
  background-color: #144d14;
  transform: translateY(-3px);
  box-shadow: 0 6px 20px rgba(26, 93, 26, 0.6);
}

.btn.secondary {
  background-color: transparent;
  color: rgb(255, 255, 255);
  border: 2px solid rgb(255, 255, 255);
}

.btn.secondary:hover {
  background-color: rgba(255, 255, 255, 0.1);
  transform: translateY(-3px);
}

/* Features Section */
.features {
  padding: 5rem 1rem;
  background: var(--light);
}

.section-header {
  text-align: center;
  margin-bottom: 3rem;
  padding: 0 1rem;
}

.section-header h2 {
  font-size: 2.5rem;
  color: var(--dark);
  margin-bottom: 1rem;
  position: relative;
  display: inline-block;
}

.section-header h2::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 4px;
  background: var(--primary);
  border-radius: 2px;
}

.section-header p {
  font-size: 1.2rem;
  color: var(--gray);
  max-width: 700px;
  margin: 1.5rem auto 0;
}

.feature-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2.5rem;
  max-width: 1200px;
  margin: 0 auto;
}

.feature-card {
  background: white;
  border-radius: 10px;
  padding: 2.5rem 2rem;
  text-align: center;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  transition: all 0.4s ease;
  position: relative;
  overflow: hidden;
}

.feature-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.15);
}

.feature-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 5px;
  background: var(--primary);
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.4s ease;
}

.feature-card:hover::before {
  transform: scaleX(1);
}

.feature-icon {
  font-size: 3.5rem;
  margin-bottom: 1.5rem;
  color: var(--primary);
  transition: transform 0.3s ease;
}

.feature-card:hover .feature-icon {
  transform: scale(1.1);
}

.feature-card h3 {
  font-size: 1.5rem;
  color: var(--dark);
  margin-bottom: 1rem;
}

.feature-card p {
  color: var(--gray);
  line-height: 1.7;
  font-size: 1.05rem;
}

/* Programs Section */
.programs {
  padding: 5rem 1rem;
  background: var(--light);
}

.programs-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(450px, 1fr));
  gap: 3rem;
  max-width: 1200px;
  margin: 0 auto;
}

.program-card {
  background: white;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  transition: all 0.4s ease;
  display: flex;
  flex-direction: column;
}

.program-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.15);
}

.program-header {
  background: var(--dark);
  color: white;
  padding: 1.5rem 2rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.program-icon {
  font-size: 2.5rem;
  color: var(--secondary);
}

.program-header h3 {
  color: white;
  margin-bottom: 0;
  font-size: 1.6rem;
}

.program-body {
  padding: 2rem;
  flex-grow: 1;
}

.program-body ul {
  list-style: none;
  margin: 1.5rem 0;
}

.program-body ul li {
  padding: 0.8rem 0;
  border-bottom: 1px solid var(--light-gray);
  display: flex;
  align-items: center;
  gap: 1rem;
}

.program-body ul li::before {
  content: '✓';
  color: var(--primary);
  font-weight: bold;
  font-size: 1.2rem;
}

.program-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem;
  background: rgba(26, 93, 26, 0.25);
  border-top: 1px solid rgba(26, 93, 26, 0.4);
}

.program-footer span {
  font-weight: 600;
  color: var(--dark);
  font-size: 1.1rem;
}

.program-footer .btn.secondary {
  background-color: var(--primary);
  color: white;
  padding: 0.8rem 1.8rem;
  border-radius: 30px;
  transition: all 0.3s ease;
}

.program-footer .btn.secondary:hover {
  background-color: #144d14;
  transform: translateY(-2px);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .hero h1 {
    font-size: 2.5rem;
  }
  
  .hero p {
    font-size: 1.3rem;
  }
  
  .programs-container {
    grid-template-columns: 1fr;
  }
  
  .hero-stats {
    gap: 2rem;
  }
}

@media (max-width: 768px) {
  .hero {
    height: auto;
    padding: 5rem 0;
  }
  
  .hero h1 {
    font-size: 2.2rem;
  }
  
  .hero p {
    font-size: 1.1rem;
  }
  
  .hero-stats {
    flex-direction: column;
    gap: 1.5rem;
    padding: 1.5rem;
  }
  
  .stat-item {
    padding: 1rem 0;
  }
  
  .feature-cards,
  .news-cards {
    grid-template-columns: 1fr;
  }
  
  .campus-tour {
    height: 50vh;
  }
  
  .tour-content h2 {
    font-size: 2.2rem;
  }
  
  .tour-content p {
    font-size: 1.1rem;
  }
}

@media (max-width: 480px) {
  .hero h1 {
    font-size: 1.8rem;
  }
  
  .hero-buttons {
    flex-direction: column;
    gap: 1rem;
  }
  
  .section-header h2 {
    font-size: 2rem;
  }
  
  .programs-container {
    grid-template-columns: 1fr;
  }
  
  .program-card {
    min-width: 100%;
  }
  
  .program-footer {
    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
  }
  
  .campus-tour {
    height: 40vh;
  }
}