/* About University Styles */
.about-university {
  max-width: 1800px;
  margin: 0 auto;
}

/* Hero Section */
.about-hero {
  background: linear-gradient(rgba(0, 0, 0, 0.6)), url('../assets/Salle1.jpg') center/cover no-repeat;
  height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: relative;
  color: white;
}

.about-hero .hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(60, 116, 83, 0.2), rgba(44, 82, 52, 0.5));
}

.about-hero .hero-content {
  position: relative;
  z-index: 2;
  max-width: 800px;
  padding: 2rem;
}

.about-hero h1 {
  font-size: 3.2rem;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.5);
}

.about-hero p {
  font-size: 1.5rem;
  text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.5);
}

/* University Presentation */
.university-presentation {
  padding: 5rem 1rem;
  background: #fff;
}

.presentation-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  max-width: 1200px;
  margin: 0 auto;
  align-items: center;
}

.presentation-text h2 {
  font-size: 2.2rem;
  margin-bottom: 2rem;
  color: var(--dark);
  position: relative;
}

.presentation-text h2::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 80px;
  height: 4px;
  background: var(--primary);
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  margin: 2rem 0;
}

.info-item {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  padding: 1rem;
  border-radius: 8px;
  background: rgba(26, 93, 26, 0.05);
}

.info-icon {
  font-size: 1.5rem;
  color: var(--primary);
  min-width: 40px;
  height: 40px;
  background: rgba(26, 93, 26, 0.1);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.info-item h3 {
  font-size: 1.1rem;
  margin-bottom: 0.3rem;
  color: var(--dark);
}

.presentation-description {
  line-height: 1.8;
  color: var(--gray);
  margin-top: 2rem;
  font-size: 1.1rem;
}

.presentation-image {
  display: flex;
  justify-content: center;
}

.university-logo {
  width: 495px;
  height: 700px;
  background: url('../assets/pres.jpg') center/contain no-repeat, var(--dark);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  border-radius: 8px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

/* Mission Section */
.mission-section {
  padding: 5rem 1rem;
  background: var(--light);
}

.mission-container {
  max-width: 1200px;
  margin: 0 auto;
}

.mission-container h2 {
  text-align: center;
  font-size: 2.2rem;
  margin-bottom: 1.5rem;
  color: var(--dark);
}

.mission-container p {
  text-align: center;
  max-width: 800px;
  margin: 0 auto 3rem;
  color: var(--gray);
  font-size: 1.1rem;
}

.mission-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2.5rem;
}

.mission-card {
  background: white;
  border-radius: 10px;
  padding: 2.5rem 2rem;
  text-align: center;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  transition: all 0.4s ease;
}

.mission-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.15);
}

.mission-icon {
  font-size: 3rem;
  margin-bottom: 1.5rem;
  color: var(--primary);
}

.mission-card h3 {
  font-size: 1.4rem;
  margin-bottom: 1rem;
  color: var(--dark);
}

.mission-card p {
  color: var(--gray);
  line-height: 1.7;
  font-size: 1.05rem;
}

/* Programs Section */
.programs-section {
  padding: 5rem 1rem;
  background: white;

}

.programs-container {
  display: flex;
  max-width: 1200px;
  margin: 0 auto;
  flex-direction: column;
}

.programs-container h2 {
  text-align: center;
  font-size: 2.2rem;
  margin-bottom: 1.5rem;
  color: var(--dark);
}

.programs-container p {
  text-align: center;
  max-width: 800px;
  margin: 0 auto 3rem;
  color: var(--gray);
  font-size: 1.1rem;
}

.program-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(450px, 1fr));
  gap: 3rem;
}

.program-card {
  background: white;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  transition: all 0.4s ease;
  display: flex;
  flex-direction: column;
}

.program-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.15);
}

.program-header {
  background: var(--dark);
  color: white;
  padding: 1.5rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.program-icon {
  font-size: 2rem;
  color: var(--secondary);
}

.program-header h3 {
  color: white;
  margin-bottom: 0;
  font-size: 1.4rem;
}

.program-body {
  padding: 2rem;
  flex-grow: 1;
}

.program-body ul {
  list-style: none;
  margin: 1.5rem 0;
}

.program-body ul li {
  padding: 0.8rem 0;
  border-bottom: 1px solid var(--light-gray);
  display: flex;
  align-items: center;
  gap: 1rem;
}

.program-body ul li::before {
  content: '✓';
  color: var(--primary);
  font-weight: bold;
  font-size: 1.2rem;
}

.program-footer {
  padding: 1.5rem 2rem;
  background: rgba(26, 93, 26, 0.1);
  border-top: 1px solid rgba(26, 93, 26, 0.2);
  text-align: center;
  font-weight: 600;
  color: var(--dark);
}

/* Campus Section */
.campus-section {
  padding: 5rem 1rem;
  background: var(--light);
}

.campus-container {
  max-width: 1200px;
  margin: 0 auto;
}

.campus-container h2 {
  text-align: center;
  font-size: 2.2rem;
  margin-bottom: 1.5rem;
  color: var(--dark);
}

.campus-container p {
  text-align: center;
  max-width: 800px;
  margin: 0 auto 3rem;
  color: var(--gray);
  font-size: 1.1rem;
}

.campus-features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2.5rem;
  margin-bottom: 4rem;
}

.campus-feature {
  background: white;
  border-radius: 10px;
  padding: 2rem;
  text-align: center;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  transition: all 0.4s ease;
}

.campus-feature:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.15);
}

.feature-icon {
  font-size: 3rem;
  margin-bottom: 1.5rem;
  color: var(--primary);
}

.campus-feature h3 {
  font-size: 1.4rem;
  margin-bottom: 1rem;
  color: var(--dark);
}

.campus-feature p {
  color: var(--gray);
  line-height: 1.7;
  font-size: 1.05rem;
  margin: 0;
}

.campus-gallery {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}

.gallery-item {
  height: 250px;
  border-radius: 10px;
  background-size: cover;
  background-position: center;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .presentation-container {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .presentation-image {
    order: -1;
  }
}

@media (max-width: 768px) {
  .about-hero h1 {
    font-size: 2.5rem;
  }
  
  .about-hero p {
    font-size: 1.2rem;
  }
  
  .program-cards {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .about-hero h1 {
    font-size: 2rem;
  }
  
  .university-logo {
    width: 310px;
    height: 440px;
  }
  
  .logo-text {
    font-size: 4rem;
  }
}