/* src/pages/Admissions.css */
.admissions-page {
  max-width: 1800px;
  margin: 0 auto;
  padding: 1rem;
}

.admissions-header {
  text-align: center;
  padding: 4rem 1rem 2rem;
}

.admissions-header h1 {
  font-size: 2.8rem;
  margin-bottom: 1rem;
}

.admissions-header p {
  font-size: 1.2rem;
  color: var(--gray);
  max-width: 800px;
  margin: 0 auto;
}

/* Tabs */
.admissions-tabs {
  display: flex;
  justify-content: center;
  margin: 2rem 0 3rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.tab-btn {
  padding: 1rem 2rem;
  background: var(--light-gray);
  border: none;
  border-radius: 4px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.tab-btn:hover {
  background: #d8d8d8;
}

.tab-btn.active {
  background: var(--primary);
  color: white;
}

/* Program Details */
.program-details {
  max-width: 1000px;
  margin: 0 auto;
  padding: 2rem;
  background: white;
  border-radius: 8px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.program-overview {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.overview-card {
  background: var(--light);
  padding: 1.5rem;
  border-radius: 8px;
  text-align: center;
}

.overview-card h3 {
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
  color: var(--primary);
}

.overview-card p {
  font-weight: 600;
}

.program-description h3 {
  font-size: 1.4rem;
  margin-top: 1.5rem;
  margin-bottom: 1rem;
  color: var(--primary);
  position: relative;
  padding-bottom: 0.5rem;
}

.program-description h3::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 50px;
  height: 3px;
  background: var(--secondary);
}

.program-description ul {
  list-style: none;
  margin-left: 1rem;
}

.program-description ul li {
  padding: 0.5rem 0;
  display: flex;
  align-items: flex-start;
}

.program-description ul li::before {
  content: '•';
  color: var(--secondary);
  font-size: 1.5rem;
  margin-right: 0.8rem;
  line-height: 1;
}

/* Application Process */
.application-process {
  max-width: 1000px;
  margin: 4rem auto;
  padding: 2rem;
  background: var(--light);
  border-radius: 8px;
}

.process-steps {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.step {
  text-align: center;
  position: relative;
  padding-top: 60px;
}

.step-number {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 50px;
  height: 50px;
  background: var(--primary);
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  font-weight: bold;
}

.step h3 {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
}

/* Documents Section */
.documents-section {
  max-width: 1000px;
  margin: 0 auto;
  padding: 2rem;
  background: white;
  border-radius: 8px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.documents-section ul {
  list-style: none;
  margin-top: 1.5rem;
}

.documents-section ul li {
  padding: 0.8rem 0;
  border-bottom: 1px solid var(--light-gray);
  display: flex;
  align-items: flex-start;
}

.documents-section ul li::before {
  content: '✓';
  color: var(--success);
  margin-right: 0.8rem;
  font-weight: bold;
}

/* Apply Section */
.apply-section {
  text-align: center;
  padding: 4rem 1rem;
  background: linear-gradient(rgba(26, 108, 63, 0.9), rgba(24, 77, 57, 0.9)), 
              url('https://images.unsplash.com/photo-1498243691581-b145c3f54a5a?q=80') center/cover;
  color: white;
  margin-top: 4rem;
}

.apply-section h2 {
  color: white;
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.apply-section p {
  font-size: 1.2rem;
  max-width: 700px;
  margin: 0 auto 2rem;
}

.apply-btn {
  padding: 1rem 3rem;
  background: var(--secondary);
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.3s ease;
}

.apply-btn:hover {
  background: #a8832d;
  transform: translateY(-2px);
}

/* Responsive Design */
@media (max-width: 768px) {
  .admissions-header h1 {
    font-size: 2.2rem;
  }
  
  .admissions-tabs {
    flex-direction: column;
  }
  
  .apply-section h2 {
    font-size: 2rem;
  }
  
  .apply-section p {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .admissions-header h1 {
    font-size: 1.8rem;
  }
  
  .program-details,
  .application-process,
  .documents-section {
    padding: 1.5rem;
  }
  
  .program-overview {
    grid-template-columns: 1fr;
  }
  
  .process-steps {
    grid-template-columns: 1fr;
  }
}