/* Navbar Styles */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: #35644e;
  color: white;
  padding: 0.8rem 2rem;
  display: flex;
  justify-content: center;
  z-index: 1000;
  transition: transform 0.3s ease, background 0.3s ease, box-shadow 0.3s ease;
  transform: translateY(0);
}

.navbar.hidden {
  transform: translateY(-100%);
}

.navbar.scrolled {
  background: rgba(44, 80, 72, 0.98);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  padding: 0.5rem 2rem;
}

.nav-container {
  width: 100%;
  max-width: 1400px;
  display: flex;
  justify-content: space-between;
}

/* Logo Styles */
.logo {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  text-decoration: none;
  color: white;
  font-weight: bold;
  transition: opacity 0.3s ease;
}

.logo:hover {
  opacity: 0.9;
}

.logo-icon {
  font-size: 1.8rem;
  color: #2beb8b;
}

.logo-text {
  display: flex;
  flex-direction: column;
  line-height: 1.2;
}

.logo-text span {
  font-size: 1.3rem;
}

.logo-text small {
  font-size: 0.75rem;
  font-weight: normal;
  opacity: 0.8;
}

.logo-img {
  height: 50px;
  width: auto;
  margin-right: 10px;
  vertical-align: middle;
}

/* Navigation Links */
.nav-links {
  display: flex;
  align-items: center;
  gap: 1.8rem;
  transition: all 0.4s ease;
}

.nav-links a {
  color: white;
  text-decoration: none;
  font-weight: 500;
  font-size: 1.05rem;
  position: relative;
  padding: 0.5rem 0;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #a8ffcc;
}

.nav-links a::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background: #00c5cc;
  transition: width 0.3s ease;
}

.nav-links a:hover::after,
.nav-links a.active::after {
  width: 100%;
}


.contact-link {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: #47996c;
  border-radius: 4px;
  text-decoration: none;
  color: white;
  transition: all 0.3s ease;

}

.contact-link:hover {
  background: #308657;
  transform: translateY(-2px);
  transition: transform 0.3s ease;
}

/* Mobile Menu Toggle */
.menu-toggle {
  display: none;
  background: none;
  border: none;
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
  transition: color 0.3s ease;
  z-index: 1001;
}

.menu-toggle:hover {
  color: #3498db;
}

/* Mobile Navigation */
@media (max-width: 900px) {
  .navbar {
    padding: 0.8rem 1.5rem;
  }
  
  .navbar.scrolled {
    padding: 0.5rem 1.5rem;
  }
  
  .nav-links {
    position: fixed;
    top: 0;
    right: -100%;
    height: 100vh;
    width: 80%;
    max-width: 350px;
    background: #2c5041;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 2rem;
    gap: 2rem;
    box-shadow: -5px 0 15px rgba(0, 0, 0, 0.1);
    transition: right 0.4s ease;
  }
  
  .nav-links.active {
    right: 0;
  }
  
  .nav-links a {
    font-size: 1.2rem;
    padding: 0.5rem 0;
  }
  
  .nav-contact {
    margin: 1rem 0 0 0;
    width: 100%;
  }
  
  .contact-link {
    justify-content: center;
    padding: 0.8rem;
  }
  
  .menu-toggle {
    display: block;
  }
  
  .logo-text span {
    font-size: 1.1rem;
  }
}

@media (max-width: 480px) {
  .logo-text span {
    font-size: 1rem;
  }
  
  .logo-text small {
    font-size: 0.65rem;
  }
}