* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

main {
  flex: 1;
  padding: 2rem;
}

/* Navbar styles */
.navbar {
  background: #2c3e50;
  color: white;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
}

.nav-links {
  display: flex;
  gap: 1.5rem;
}

.nav-links a {
  color: white;
  text-decoration: none;
  transition: opacity 0.3s;
}

.nav-links a:hover {
  opacity: 0.8;
}

/* Footer styles */
.footer {
  background: #34495e;
  color: white;
  padding: 2rem;
  text-align: center;
}

/* Contact page styles */
.contact-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

@media (max-width: 768px) {
  .contact-container {
    grid-template-columns: 1fr;
  }
}

/* src/index.css - Add these styles */

/* Home Page Styles */
.home-page {
  max-width: 1700px;
  margin: 0 auto;
}

/* Hero Section */
.hero {
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0,0,0,0.5)), url('https://images.unsplash.com/photo-1523240795612-9a054b0db644?q=80') center/cover;
  height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  position: relative;
}

.hero-content {
  max-width: 800px;
  padding: 2rem;
  z-index: 2;
}

.hero h1 {
  font-size: 3.5rem;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.hero p {
  font-size: 1.5rem;
  margin-bottom: 2rem;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
}

.hero-buttons {
  display: flex;
  justify-content: center;
  gap: 1rem;
  flex-wrap: wrap;
}

/* Button Styles */
.btn {
  padding: 0.8rem 1.5rem;
  border-radius: 4px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  display: inline-block;
}

.btn.primary {
  background-color: #2c5041;
  color: white;
  border: 2px solid #2c5047;
}

.btn.primary:hover {
  background-color: #1a2f28;
  transform: translateY(-2px);
}

.btn.secondary {
  background-color: transparent;
  color: white;
  border: 2px solid white;
}

.btn.secondary:hover {
  background-color: rgba(255, 255, 255, 0.1);
  transform: translateY(-2px);
}

/* Section Styling */
.section-header {
  text-align: center;
  margin: 4rem 0 3rem;
}

.section-header h2 {
  font-size: 2.5rem;
  color: #2c3e50;
  margin-bottom: 1rem;
}

.section-header p {
  font-size: 1.2rem;
  color: #7f8c8d;
  max-width: 700px;
  margin: 0 auto;
}

/* Features Section */
.features {
  padding: 2rem;
  background-color: #f9f9f9;
}

.feature-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.feature-card {
  background: white;
  border-radius: 8px;
  padding: 2rem;
  text-align: center;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.feature-card:hover {
  transform: translateY(-10px);
}

.feature-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.feature-card h3 {
  color: #2c3e50;
  margin-bottom: 1rem;
}

.feature-card p {
  color: #7f8c8d;
  line-height: 1.6;
}

/* About Section */
.about {
  padding: 4rem 2rem;
}

.about-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  max-width: 1200px;
  margin: 0 auto;
  align-items: center;
}

.about-text h2 {
  font-size: 2.5rem;
  color: #2c3e50;
  margin-bottom: 1.5rem;
}

.about-text p {
  margin-bottom: 1.5rem;
  line-height: 1.7;
  color: #34495e;
}

.about-image {
  position: relative;
  height: 400px;
  border-radius: 8px;
  overflow: hidden;
}

.image-placeholder {
  background: linear-gradient(rgba(44, 80, 69, 0.7), rgba(44, 80, 69, 0.7)), 
              url('https://images.unsplash.com/photo-1523240795612-9a054b0db644?q=80') center/cover;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.5rem;
  font-weight: bold;
  text-align: center;
}

.overlay-text {
  padding: 2rem;
}

/* Testimonials Section */
.testimonials {
  background-color: #f5f7fa;
  padding: 4rem 2rem;
}

.testimonial-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.testimonial-card {
  background: white;
  border-radius: 8px;
  padding: 2rem;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.testimonial-card p {
  font-style: italic;
  line-height: 1.7;
  color: #345e4e;
  margin-bottom: 1.5rem;
  position: relative;
}

.testimonial-card p::before {
  content: "“";
  font-size: 4rem;
  position: absolute;
  top: -25px;
  left: -10px;
  color: #ecf0f1;
  font-family: serif;
  z-index: 0;
}

.testimonial-author {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.author-avatar {
  width: 50px;
  height: 50px;
  background-color: #2c3e50;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}

.author-info {
  display: flex;
  flex-direction: column;
}

.author-info strong {
  color: #2c5041;
}

.author-info span {
  color: #7f8c8d;
  font-size: 0.9rem;
}

/* Call to Action Section */
.cta {
  background: linear-gradient(rgba(44, 80, 65, 0.9), rgba(44, 80, 69, 0.9)), 
              url('https://images.unsplash.com/photo-1543269865-cbf427effbad?q=80') center/cover;
  color: white;
  text-align: center;
  padding: 6rem 2rem;
}

.cta-content {
  max-width: 800px;
  margin: 0 auto;
}

.cta h2 {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
}

.cta p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.cta-buttons {
  display: flex;
  justify-content: center;
  gap: 1rem;
  flex-wrap: wrap;
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero h1 {
    font-size: 2.5rem;
  }
  
  .hero p {
    font-size: 1.2rem;
  }
  
  .about-content {
    grid-template-columns: 1fr;
  }
  
  .cta-buttons, .hero-buttons {
    flex-direction: column;
    align-items: center;
  }
}