/* src/components/Footer.css */
.footer {
  background: var(--primary);
  color: #ecf0f1;
  position: relative;
  overflow: hidden;
  padding-top: 4rem;
}

.footer-wave {
  position: absolute;
  top: -10px;
  left: 0;
  width: 100%;
  transform: rotate(180deg);
}

.footer-wave svg {
  display: block;
  width: 100%;
  height: 80px;
}

.footer-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2.5rem;
  max-width: 1800px;
  margin: 0 auto;
  padding: 0 2rem 3rem;
  position: relative;
  z-index: 2;
}

.footer-column {
  display: flex;
  flex-direction: column;
}

.footer-logo {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.footer-logo-icon {
  font-size: 2.5rem;
  color: var(--secondary);
}

.footer-logo-text h3 {
  font-size: 1.5rem;
  margin-bottom: 0.3rem;
  color: white;
}

.footer-logo-text span {
  font-size: 0.9rem;
  color: #bdc3c7;
}

.footer-description {
  line-height: 1.7;
  margin-bottom: 1.5rem;
  color: #bdc3c7;
}

.footer-social {
  display: flex;
  gap: 1rem;
  margin-top: 0.5rem;
}

.footer-social a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  color: white;
  font-size: 1.1rem;
  transition: all 0.3s ease;
}

.footer-social a:hover {
  background: var(--secondary);
  transform: translateY(-3px);
}

.footer-title {
  position: relative;
  font-size: 1.3rem;
  margin-bottom: 1.8rem;
  padding-bottom: 0.8rem;
  color: white;
}

.footer-title::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 50px;
  height: 3px;
  background: var(--secondary);
  border-radius: 2px;
}

.footer-links {
  list-style: none;
}

.footer-links li {
  margin-bottom: 0.8rem;
}

.footer-links a {
  color: #bdc3c7;
  text-decoration: none;
  transition: all 0.3s ease;
  display: inline-block;
  position: relative;
  padding-left: 1.5rem;
}

.footer-links a::before {
  content: '→';
  position: absolute;
  left: 0;
  color: var(--secondary);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.footer-links a:hover {
  color: white;
  transform: translateX(5px);
}

.footer-links a:hover::before {
  opacity: 1;
}

.footer-contact {
  list-style: none;
}

.footer-contact li {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.2rem;
  align-items: flex-start;
}

.footer-contact-icon {
  color: var(--secondary);
  font-size: 1.1rem;
  min-width: 20px;
  margin-top: 0.2rem;
}

.newsletter-text {
  margin-bottom: 1.5rem;
  line-height: 1.7;
  color: #bdc3c7;
}

.newsletter-form {
  display: flex;
  margin-bottom: 2.5rem;
}

.newsletter-form input {
  flex: 1;
  padding: 0.9rem 1.2rem;
  border: none;
  border-radius: 4px 0 0 4px;
  font-size: 1rem;
  outline: none;
}

.newsletter-form button {
  background: var(--secondary);
  color: white;
  border: none;
  padding: 0 1.5rem;
  font-weight: 600;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  transition: background 0.3s ease;
}

.newsletter-form button:hover {
  background: #a8832d;
}

.prayer-times {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  padding: 1.2rem;
}

.prayer-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.8rem;
  margin-top: 0.8rem;
}

.prayer-grid div {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.prayer-grid div:last-child,
.prayer-grid div:nth-last-child(2) {
  border-bottom: none;
}

.prayer-grid span:first-child {
  color: white;
  font-weight: 500;
}

.prayer-grid span:last-child {
  color: var(--secondary);
}

.footer-bottom {
  background: rgba(0, 0, 0, 0.2);
  padding: 1.5rem 2rem;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
}

.footer-bottom-content {
  max-width: 1400px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1rem;
  text-align: center;
}

.footer-bottom p {
  font-size: 0.9rem;
  color: #bdc3c7;
  align-items: center;
}

/* Responsive Design */
@media (max-width: 768px) {
  .footer-content {
    grid-template-columns: 1fr;
    gap: 3rem;
  }
  
  .footer-bottom-content {
    flex-direction: column;
    text-align: center;
  }
  
  .footer-extra-links {
    margin-top: 0.5rem;
  }
  
  .prayer-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .newsletter-form {
    flex-direction: column;
  }
  
  .newsletter-form input,
  .newsletter-form button {
    width: 100%;
    border-radius: 4px;
    margin-bottom: 0.5rem;
  }
  
  .footer-links a {
    padding-left: 0;
  }
  
  .footer-links a::before {
    display: none;
  }
}